<template>
  <div id="home">
    <div>
      <ul class="collection with-header">
        <li v-for="ad in ads" v-bind:key="ad.id" class="collection-item">
          <router-link v-bind:to="{name:'view-ad', params: {ad_id: ad.ad_id}}"><div class="chip orange darken-3 white-text">{{ad.category}}</div></router-link>
          <div class="right">
            {{ad.date}}
            <router-link v-bind:to="{name:'view-ad', params: {ad_id: ad.ad_id}}"><i class="fa fa-chevron-right fa-lg"></i></router-link><br>
          </div>
                      
          <div class="ad">{{ad.ad}}</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
import firebase from 'firebase';
import moment from 'moment';

moment.locale('es');

export default {
  name: 'home',
  data(){
    return{
      ads: []
    }
  },
  created(){
    db.collection('ads').orderBy('date','desc').get()
    //db.collection('ads').orderBy('date','desc').get()
      .then(querySnapshot => {
        this.loading = false;
          querySnapshot.forEach(doc => {
            const data = {
              ad_id: doc.data().ad_id,
              category: doc.data().category,
              ad: doc.data().ad,
              cellphone: doc.data().cellphone,
              date: moment(doc.data().date).fromNow()
            };
            this.ads.push(data);
          });
      });
  }
}
</script>
<style scoped>
.fa-chevron-right{
  color: #ef6c00;
}
.ad{
  display: block;
  display: -webkit-box;
  height: 14px * 1.4 * 3;
  font-size: 14px;
  line-height: 1.4;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>




