import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import ViewAd from '@/components/ViewAd'
import CategoryAd from '@/components/CategoryAd'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/:ad_id',
      name: 'view-ad',
      component: ViewAd
    },
    {
      path: '/:category',
      name: 'category',
      component: CategoryAd
    }
  ]
})



