<template>
  <div id="view-ad">
    <ul class="collection with-header">
      <li class="collection-item"><div class="chip orange darken-3 white-text">{{category}}</div></li>
      <li class="collection-item">{{ad}}</li>
      <li class="collection-item"><div class="chip orange darken-3 white-text"><i class="fa fa-phone"></i></div>{{cellphone}}</li>
      <li class="collection-item"><div class="chip orange darken-3 white-text"><i class="fa fa-calendar"></i></div>Publicado {{date}}</li>
      <li class="collection-item"><div class="chip grey darken-2 white-text"><i class="fa fa-user-circle"></i></div>Publicado en {{reseller}} </li>
    </ul>
    <router-link to="/" class="btn green darken-2"><i class="fa fa-chevron-left"></i> Volver</router-link>
  </div>
</template>

<script>
import db from './firebaseInit';
import moment from 'moment';

export default {
  name: 'view-ad',
    data(){
      return{
        ad_id: null,
        category: null,
        ad: null,
        cellphone: null,
        reseller: null,
        date: null
      }
    },
    beforeRouteEnter(to, from, next){
      db.collection('ads').where('ad_id','==',to.params.ad_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.ad_id = doc.data().ad_id
              vm.category = doc.data().category
              vm.ad = doc.data().ad
              vm.cellphone = doc.data().cellphone
              vm.reseller = doc.data().reseller
              vm.date = moment(doc.data().date).fromNow()
            })
          })
        })
    },
    watch:{
      '$route':'fetchData'
    },  
    methods:{
      fetchData(){
        db.collection('ads').where('ad_id','==',this.$route.params.ad_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.ad_id = doc.data().ad_id
              this.category = doc.data().category
              this.ad = doc.data().ad
              this.cellphone = doc.data().cellphone
              this.reseller = doc.data().reseller
              this.date = doc.data().date
            })
          })
      }
    }
}

function cw(str){
  return str.split('').length;
}
</script>



